<template>
  <div id="app">
    <ModalComponent
      v-if="isOpenModal"
    />
    <LimitModal
      v-if="isLimitExceeded"
    />
    <MainView />
  </div>
</template>

<script>
import MainView from '@/views/MainView.vue'
import axios from 'axios'
import { mapActions, mapGetters } from 'vuex'
import LimitModal from '@/views/modals/LimitModal.vue'
import ModalComponent from '@/views/modals/Modal.vue'
export default {
  name: 'App',
  components: {
    MainView,
    LimitModal,
    ModalComponent
  },
  data () {
    return {
    }
  },
  mounted() {
    this.getLocIp()
  },
  computed: {
    ...mapGetters('weatherData', [
      'isOpenModal',
      'isLimitExceeded'
    ])
  },
  methods: {
    ...mapActions('weatherData', ['getWeatherByGeo']),
    async getLocIp () {
      try {
        const response = await axios.get(`https://ipinfo.io/json?token=${process.env.VUE_APP_TOKEN}`)
        const { loc } = response.data
        const [latitude, longitude] = loc.split(',')
        await this.getWeatherByGeo({ lat: latitude, lon: longitude, local: this.$i18n.locale })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style lang="scss">
@import "../css/globalCSS.css";
#app {
  display: flex;
  justify-content: center;
}
</style>
